nav {
  float: right;
  text-align: right;
}

nav ul {
  margin-left: 20px;
  margin: 0;
  padding: 0;
  list-style: none;
}

nav li {
  display: inline-block;
  margin-right: 30px;
  margin-left: 45px;
  padding-top: 23px;
  position: relative;
}

nav a {
  text-decoration: none;
  text-transform: uppercase;
  font-size: 30px;
  color: #d5f8f2;
}

nav a:hover {
  color: #81c0c2;
}

nav a::before {
  content: '';
  display: block;
  height: 5px;
  background-color: #81c0c2;

  position: absolute;
  top: 0;
  width: 0%;

  transition: all ease-in-out 250ms;
}

nav a:hover::before {
  width: 100%;
}

.main-contaner {
  display: grid;
  height: 100vh;
  width: auto;
  background-image: url(/new-portfolio/static/media/20200229_164836.9d6dba6e.jpg);
  background-size: 100% 100%;
  background-repeat: no-repeat;
}
.header {
  display: flex;
  margin: auto;
  margin-top: 10px;
  position: absolute;
  margin-left: 10px;
}
.title h1 {
  font-size: 100px;
  color: #d5f8f2;
  text-align: center;
  padding-top: 40px;
}
.title p {
  font-size: 18px;
  text-align: center;
  color: #646464;
  position: relative;
  top: -45px;
}
.title {
  margin-top: 0;
}

.media {
  position: relative;
  top: 2.5vh;
  text-align: center;
}

.media img {
  opacity: 0.7;
  padding-right: 15px;
  padding-left: 15px;
  -webkit-filter: invert(1);
          filter: invert(1);
}

.media img:hover {
  opacity: 1;
}

.about-me-container {
  height: 100vh;
  width: auto;
  text-align: center;
  padding-top: 5vh;
  background-color: #408486;
  color: #d5f8f2;
}

.my-p {
  height: 50vh;
  width: auto;
}
.my-p img {
  border-radius: 50%;
  height: 100%;
  width: auto;
}

.about-me-container p {
  font-size: 25px;
  position: relative;
  top: 10px;
}

.portfolio {
  overflow: hidden;
  height: 100vh;
  width: auto;
  background-color: #57abae;
  /* flex-wrap: wrap; */
}

.portfolio h2 {
  color: #d5f8f2;
}

.portfolio img {
  margin: 10px;
  width: 300px;
  height: 170px;
}

.projects {
  height: 70vh;
  display: flex;
  justify-content: center;
  align-items: center;
  align-content: center;
}

.projects a {
  width: auto;
  height: 170px;
}

.project-card:hover .hover {
  opacity: 0.8;
}

.projects h2 {
  padding: 5px;
  color: #646464;
  font-size: 30px;
  width: auto;
}

.project-card {
  height: 170px;
}
.hover {
  display: flex;
  justify-content: center;
  align-items: center;
  align-content: center;
  position: relative;
  top: -196.5px;
  margin: 10px;
  width: 300px;
  height: 170px;
  background-color: #f2f9f9;
  opacity: 0;
}

.contact {
  text-align: center;
  color: #408486;
}

.contact ul {
  text-align: left;
  /* width: 40vw; */
  mix-blend-mode: normal;
  position: relative;
  right: 30%;
  left: 30%;
}

.contact a {
  color: #408486;
}
.contact a:hover {
  color: #86c6c9;
}
.line {
  margin-top: 30px;
  margin-bottom: 10px;
  width: 90vw;
  height: 1px;
  mix-blend-mode: normal;
  position: relative;
  right: 5%;
  left: 5%;
  border: 1px solid#408486;
}

.footer {
  font-size: 10px;
  color: #646464;
  margin: 10px;
}

.go-top {
  position: fixed;
  bottom: 2vh;
  right: 1vw;
}

.go-top a {
  color: #bae2e4;
}

.go-top a:hover {
  color: #2e6264;
}

.go-top a::before {
  content: '';
  display: block;
  height: 3px;
  background-color: #2e6264;
  position: absolute;
  bottom: 0;
  width: 0%;

  transition: all ease-in-out 250ms;
}

.go-top a:hover::before {
  width: 35px;
}

